body.show-modal .backdrop {
   opacity: 1;
   visibility: visible;
   pointer-events: initial;
}

body.show-modal .modal_windowl {
   transform: scale(1);
}

.backdrop {
   display: flex;
   justify-content: center;
   align-items: center;
   position: fixed;
   top: 0;
   left: 0;
   width: 100vw;
   height: 100%;
   background-color: var(--shadow-color);
   z-index: 777;

   pointer-events: none;
   opacity: 0;
   visibility: hidden;
   transition: opasity 200ms ease-in-out, visibility 200ms ease-in-out;
}

.modal_window {
   width: 100%;
   background-color: var(--light-color);

   font-family: 'Roboto';
   font-style: normal;
   font-weight: 500;
   color: var(--dark-color);
   margin: 0 auto;

   transform: scale(1.1);
   transition: transform 200ms ease-in-out;
}

.modal_btn_close {
   position: absolute;
   top: 8px;
   right: 8px;
   padding: 0;
   line-height: 0;
   width: 30px;
   height: 30px;
   //background-color: var(--light-color);
}

.modal_btn_close:hover,
.modal_btn_close:focus {
   border: 1px solid var(--dark-color);
}

.modal_icon_close {
   height: 14px;
   width: 14px;
   fill: var(--dark-color);
}

.modal_img {
   margin-bottom: 0;
   border-radius: 5px;
   box-shadow: 0px 4px 4px var(--shadow-color);
}

.modal_title {
   font-size: 20px;
   line-height: 1.15;
   text-transform: uppercase;
   margin-bottom: 20px;
}

.modal_tbl {
   table-layout: fixed;
   width: 100%;
   margin-bottom: 20px;
}

.modal_tbl_left-side {
   font-size: 12px;
   line-height: 1.33;
   color: var(--gray-color);
}

.modal_tbl_right_top {
   font-size: 12px;
   line-height: 1.17;
}

.modal_tbl_right_top.vote {
   background-color: var(--active-btn-color);
   border-radius: 5px;
   text-align: center;
   color: var(--light-color);
}

.modal_tbl_right_title {
   font-size: 12px;
   line-height: 1.33;
   text-transform: uppercase;
}

.modal_tbl_right_genre {
   font-size: 12px;
   line-height: 1.33;
}

.modal_title_text {
   font-size: 12px;
   line-height: 1.33;
   text-transform: uppercase;
   margin-bottom: 8px;
}

.modal_text {
   font-size: 12px;
   line-height: 1.67;
   margin-bottom: 20px;
   overflow: auto;
}

.btn_list {
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.btn_choice {
   height: 44px;
   font-size: 12px;
   line-height: 1.33;
   text-transform: uppercase;
   justify-content: center;
   border-radius: 5px;
   border: solid 1px var(--dark-color);
   padding: 8px;
}

.btn_choice:hover,
.btn_choice:focus {
   background-color: var(--active-btn-color);
   color: var(--light-color);
   border: none;
}

.hidden {
   opacity: 0;
   visibility: hidden;
}

/*mob*/
@media screen and (max-width: 767px) {
   .modal_window {
      height: 85vh;
      padding: 48px 20px 40px 20px;
      max-width: 280px;
      overflow-y: scroll;
   }

   .modal_img {
      width: 240px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 20px;
   }

   .modal_description {
      max-width: 240px;
      margin: 0 auto;
   }

   .modal_text {
      max-height: 236px;
   }

   .btn_choice {
      width: 110px;
   }
}

/*tab*/
@media screen and (min-width: 768px) {
   .modal_window {
      padding: 40px 72px 40px 36px;
      max-width: 704px;
   }

   .modal_img {
      width: 264px;
      height: 374px;
      margin-right: 68px;
   }

   .modal_without_close-btn {
      display: flex;
   }

   .modal_text {
      max-height: 236px;
   }

   .btn_choice {
      width: 125px;
   }
}

/*deck*/
@media screen and (min-width: 1200px) {
   .modal_window {
      padding: 40px 12px 40px 12px;
      max-width: 806px;
   }

   .modal_img {
      width: 375px;
      height: 478px;
      margin-right: 16px;
   }

   .modal_title {
      font-size: 30px;
      line-height: 1.17;
   }

   .modal_text {
      max-height: 160px;
   }

   .btn_choice {
      width: 136px;
   }
}

// ---------------------section gallery---------------------

.section {
   padding-top: 20px;
   padding-bottom: 40px;
}

.card-img {
   border-radius: 5px;
   width: 280px;
   height: 398px;
}
.gallery-card {
   height: 100%;
   transform: translate(0px, 20%) scale(0.5);
   opacity: 0;
   transition: transform 0.8s ease 0s;
}
.gallery-card._active {
   transform: translate(0px, 0px) scale(1);
   opacity: 1;
}

.card-link {
   display: block;
   transition: box-shadow var(--options-for-transition),
      background-color var(--options-for-transition), transform var(--options-for-transition);
   border-radius: 5px;
}
.card-link:hover,
.card-link :focus {
   display: block;
   transform: scale(0.98);
   background-color: var(--background-color-for-hover);
   box-shadow: var(--box-shadow-color);
}

.card-title {
   margin-bottom: 8px;
   margin-top: 8px;
   margin-right: auto;
   overflow-wrap: break-word;
   text-transform: uppercase;
   font-weight: 500;
   color: var(--dark-color);
}

.card-average {
   width: 36px;
   height: 16px;
   padding: 1px 6px;
   border-radius: 5px;
   font-size: 12px;
   margin-left: 4px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;

   color: var(--light-color);
   background: var(--active-btn-color);
}

.card-ganre {
   margin-right: 4px;

   color: var(--active-btn-color);
}
.card-position {
   display: flex;
   align-items: baseline;
}

.card-release {
   padding-left: 4px;
   border-left: 1px solid var(--active-btn-color);
   color: var(--active-btn-color);
}

.card-ganres,
.card-release {
   font-size: 12px;
   font-weight: 500;
   line-height: 1.16;
}

@media screen and (max-width: 767px) {
   .gallery-list {
      display: flex;
      flex-direction: column;
      align-items: center;
   }

   .card-release {
      margin-bottom: 20px;
   }

   .except-mobile {
      display: none;
   }
}

@media screen and (min-width: 768px) {
   .container {
      width: 768px;
   }

   .card-average {
      width: 40px;
      height: 20px;
   }

   .gallery-list {
      display: flex;
      flex-wrap: wrap;
      gap: 32px;
   }

   .card-img {
      width: 336px;
      height: 455px;
   }

   .section {
      padding-top: 60px;
      padding-bottom: 60px;
   }

   .gallery-card {
      width: calc((100% - 32px) / 2);
      height: 495px;
   }

   .pagination__dots {
      cursor: default;
   }
}

@media screen and (min-width: 1280px) {
   .container {
      width: 1280px;
   }
   .card-img {
      width: 394px;
      height: 574px;
   }

   .gallery-card {
      width: calc((100% - 32px) / 3);
      height: 634px;
   }

   .gallery-list {
      column-gap: 16px;
      row-gap: 32px;
   }
   .card-title,
   .card-release,
   .card-ganre {
      font-size: 20px;
      line-height: 1.2;
   }

   .card-position {
      align-items: center;
   }
}

// --------------------------pagination--------------------------
.pagination {
   width: 100%;
   display: flex;
   justify-content: center;
   margin-top: 60px;
}

.pagination__list {
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 4px;
}

.pagination__item {
   height: 40px;
   display: flex;
   justify-content: center;
   align-items: center;
   border: none;
   background-color: inherit;
   padding: 0 8px;
}

.pagination__nav-btn {
   width: 40px;
   padding: 0;
   background-color: #dedcdc;
   border-radius: 5px;
   transition: background-color 250ms linear;

   &:hover,
   &:focus {
      outline: none;
      background-color: var(--active-btn-color);
   }
}

.pagination__nav-btn_right {
   transform: rotateY(180deg);
}

.pagination__icon {
   width: 14px;
   height: 14px;
   fill: #000;
   transition: fill 150ms linear;

   .pagination__nav-btn:hover &,
   .pagination__nav-btn:focus & {
      fill: #fff;
   }
}

.pagination__number {
   border-radius: 5px;
   transition: background-color 250ms linear, color 150ms linear;
}

.pagination__number:hover,
.pagination__number:focus {
   outline: none;
   background-color: var(--active-btn-color);
   color: #fff;
}

.pagination__number_current {
   min-width: 40px;
   background-color: var(--active-btn-color);
   color: #fff;
   border-radius: 5px;
   cursor: default;
}

.gallery-wrapper {
   position: relative;
}

.preloader-wrapper {
   display: flex;
   justify-content: center;
   align-items: center;
   width: 100%;
   height: 100%;
   z-index: 100;
}

.preloader-wrapper.preloader-hidden {
   display: none;
}

.preloader {
   display: inline-block;
   position: relative;
   width: 80px;
   height: 80px;
}
.preloader div {
   position: absolute;
   width: 6px;
   height: 6px;
   background: var(--active-btn-color);
   border-radius: 50%;
   animation: preloader 1.2s linear infinite;
}
.preloader div:nth-child(1) {
   animation-delay: 0s;
   top: 37px;
   left: 66px;
}
.preloader div:nth-child(2) {
   animation-delay: -0.1s;
   top: 22px;
   left: 62px;
}
.preloader div:nth-child(3) {
   animation-delay: -0.2s;
   top: 11px;
   left: 52px;
}
.preloader div:nth-child(4) {
   animation-delay: -0.3s;
   top: 7px;
   left: 37px;
}
.preloader div:nth-child(5) {
   animation-delay: -0.4s;
   top: 11px;
   left: 22px;
}
.preloader div:nth-child(6) {
   animation-delay: -0.5s;
   top: 22px;
   left: 11px;
}
.preloader div:nth-child(7) {
   animation-delay: -0.6s;
   top: 37px;
   left: 7px;
}
.preloader div:nth-child(8) {
   animation-delay: -0.7s;
   top: 52px;
   left: 11px;
}
.preloader div:nth-child(9) {
   animation-delay: -0.8s;
   top: 62px;
   left: 22px;
}
.preloader div:nth-child(10) {
   animation-delay: -0.9s;
   top: 66px;
   left: 37px;
}
.preloader div:nth-child(11) {
   animation-delay: -1s;
   top: 62px;
   left: 52px;
}
.preloader div:nth-child(12) {
   animation-delay: -1.1s;
   top: 52px;
   left: 62px;
}

@keyframes preloader {
   0%,
   20%,
   80%,
   100% {
      transform: scale(1);
   }
   50% {
      transform: scale(1.5);
   }
}
