  top: 11px;
  left: 52px;
}

.preloader div:nth-child(4) {
  animation-delay: -.3s;
  top: 7px;
  left: 37px;
}

.preloader div:nth-child(5) {
  animation-delay: -.4s;
  top: 11px;
  left: 22px;
}

.preloader div:nth-child(6) {
  animation-delay: -.5s;
  top: 22px;
  left: 11px;
}

.preloader div:nth-child(7) {
  animation-delay: -.6s;
  top: 37px;
  left: 7px;
}

.preloader div:nth-child(8) {
  animation-delay: -.7s;
  top: 52px;
  left: 11px;
}

.preloader div:nth-child(9) {
  animation-delay: -.8s;
  top: 62px;
  left: 22px;
}

.preloader div:nth-child(10) {
  animation-delay: -.9s;
  top: 66px;
  left: 37px;
}

.preloader div:nth-child(11) {
  animation-delay: -1s;
  top: 62px;
  left: 52px;
}

.preloader div:nth-child(12) {
  animation-delay: -1.1s;
  top: 52px;
  left: 62px;
}

@keyframes preloader {
  0%, 20%, 80%, 100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.5);
  }
